import Endpoint from '@/services/Endpoint'

export default {
  banks (payload) {
    return Endpoint().get('/s1/bank/bank', {
      params: payload
    })
  },
  createBank (payload) {
    return Endpoint().post('/s1/bank/bank', payload)
  },
  modifyBank (payload) {
    return Endpoint().patch('/s1/bank/bank', payload)
  },
  importBic (payload) {
    return Endpoint().post('/s1/bank/bank/import/bic', payload.formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  importText (payload) {
    return Endpoint().post('/s1/bank/bank/import/text', payload.formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  }
}
